import { Box, ListItem, ListItemText } from "@mui/material";
import React, { useCallback } from "react";
import { Link as RouterLink } from "react-router-dom";
import { Link as MuiLink } from "@mui/material";
import { useNavigate } from "react-router-dom";

export default function SearchResultItem({ option, ...props }) {
  const navigate = useNavigate();
  const optionPath = `/tejan-alnoor/students/${option?.id}`;
  const handleOptioClick = useCallback(() => {
    const existingList = JSON.parse(
      localStorage.getItem("recent-seraches") ?? "[]"
    );

    const newList = [
      option.id,
      ...existingList.filter((id) => id !== option.id),
    ].slice(0, 7);

    localStorage.setItem("recent-seraches", JSON.stringify(newList));
  }, [option?.id]);

  if (!option) return null;

  return (
    <ListItem
      {...props}
      onClick={() => {
        navigate(optionPath);
        handleOptioClick();
      }}
    >
      <Box width="100%">
        <ListItemText
          primary={
            <MuiLink
              component={RouterLink}
              to={optionPath}
              sx={{ textDecoration: "none" }}
              onClick={() => handleOptioClick()}
            >
              {option.name}
            </MuiLink>
          }
          secondary={`${option.level} -  ${option.supervisor}`}
          primaryTypographyProps={{ variant: "h6" }}
        />
      </Box>
    </ListItem>
  );
}
