import {
  Autocomplete,
  Box,
  colors,
  Divider,
  lighten,
  List,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import React from "react";
import _ from "lodash";
import { useStudents } from "../../hooks/useData";
import FuzzySearch from "fuzzy-search";
import SearchResultItem from "./searchResultItem";

export default function SearchPage() {
  const { data: students, loading } = useStudents(); // This will fetch the data from the Google Sheet

  const recentSearches = JSON.parse(
    localStorage.getItem("recent-seraches") ?? "[]"
  );

  const filterOptions = (options, { inputValue }) => {
    const searcher = new FuzzySearch(options ?? [], ["name"], {
      caseSensitive: true,
      sort: true,
    });
    return searcher.search(inputValue);
  };

  return (
    <Stack>
      <Autocomplete
        dir="rtl"
        disablePortal
        options={students}
        sx={{ width: "100%" }}
        renderInput={(params) => (
          <TextField {...params} label="  " variant="standard" />
        )}
        getOptionLabel={(option) => option.name}
        filterOptions={filterOptions}
        renderOption={(props, option) => (
          <Box key={props.key}>
            <SearchResultItem option={option} {..._.omit(props, "key")} />
            <Divider />
          </Box>
        )}
      />

      {recentSearches.length > 0 && (
        <Stack
          mt={7}
          py={3}
          bgcolor={lighten(colors.teal[50], 0.3)}
          borderRadius={1}
        >
          <Typography
            variant="h5"
            sx={{
              px: 2,
              width: "fit-content",
              pr: 5,
            }}
          >
              
            <Divider />
          </Typography>

          <List>
            {recentSearches.map((id) => (
              <Box key={id}>
                <SearchResultItem
                  option={students.find((student) => student.id === id)}
                  sx={{ px: 2 }}
                />
              </Box>
            ))}
          </List>
        </Stack>
      )}
    </Stack>
  );
}
